.collection-item{
    width: 22vw;
    display: flex;
    flex-direction: column;
    height: 350px;
    align-items: center;
    position: relative;
    .image{
        width: 100%;
        height: 95%;
        margin-bottom:5px;
        background-position: center;
        background-size: cover;
    }
    .custom-button{
        width: 80%;
        opacity: 0.7;
        position: absolute;
        top:255px;
        display: none;
    }
    &:hover{
        .image{
            opacity: 0.8;            
        }
        .custom-button{
            opacity: 0.85;
            display: flex;
        }
    }
    .collection-footer{
        width: 100%;
        height: 5%;
        font-size: 18px;
        display: flex;
        justify-content: space-between;
        .name{
            width: 85%;
            margin-bottom: 15px;
        }
        .price{
            width:15%
        }
    }
}


