.menu-item{
    min-width: 30%;
    height: 240px;
    flex:1 1 auto;
    display: flex;
    align-items:center;
    justify-content: center;
    border: 1px solid black;
    margin: 0 7.5px 15px;
    overflow: hidden;
    
    
    
    &.large{
        height: 380px;
    }
    
    &:first-child{
        margin-right: 7.5px;
    }
    &:last-child{
        margin-left: 7.5px;
    }

    &:hover{
        cursor: pointer;
      & .backgroundImage{
            transform: scale(1.1);
            transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
        }
        & .content{
            background-color: rgba(255, 255, 255, 0.9);
        }
    }
    .content{
        background-color: rgba(255, 255, 255, 0.6);
        position: absolute;
    }
    .backgroundImage{
        width: 100%;
        height: 100%;
        background-position: center;
        background-size: cover;
    }
}